body{
    margin: 0;
    padding: 0;
}
.Header{
    width: 100%;
    height: 500px;
    background: #292626;
    color: white;
}
.Logo{
    width: 97%;
    height: 45px;
    font-size: 1.5rem;
    padding: 5px;
    border-bottom: 1px ridge white;
}
.Content{
    text-align: center;
    align-items: center;
    flex-wrap: wrap;
    display: flex;
}
.Content img{
    margin-top: 90px;
    width: 500px;
}
@media (max-width: 1200px){
    .Content img{
        margin: 0px;
        width: 100%;
    }
    .Content h2{
        width: 100%;
        font-size: 1.3rem;
    }

}
.Content h2{
    margin-left: 50px;
    font-family: arial;
    font-size: 2rem;
}