.Inputs{
    border: 2px solid rgba(0, 0, 0, 0.658);
    width: 500px;
    height: 300px;
    margin: 0 auto;
    margin-top: 20px;
    box-shadow: 1px 1px 2px gray;
}
@media(max-width: 1200px){
    .Inputs{
        width: 350px;
    }
}
.Inputs h3{
    font-family: Georgia;
    text-align: center;
    font-size: 1.4rem;
}
.Inputs input{
    padding: 3px;
    width: 70%;
    border: 1px solid rgb(6, 2, 48);
}
.Inputs button{
    border-radius: 5%;
    border: none;
    background: rgb(67, 67, 240);
    padding: 10px;
    color: white;
    font: normal 18px arial ;
    margin-top: 10px;
    margin-left: 30%;
    width: 50%;
}
.Inputs button:hover{
    transition: 0.4s;
    background: rgb(0, 0, 250);
    color: rgb(241, 172, 172);
}
.Inputs p{
    float: left;
    width: 100%;
    font-size: 1.3rem;
    margin: 5px;
    font-family: arial;
}