
.Functions{
    height: 500px;
    width: 100%;
    background: #302d2d ;
    color: white;
}
.TextBlock{
    display: flex;
    flex-wrap: wrap;
    margin-left: 100px;
}
.TextBlock div{
    text-align: center;
    padding: 5px;
    margin: 10px;
    border: 1px solid rgb(255, 255, 255);
    height: 230px;
    max-width: 300px;
    width: 200px;
    flex: 1 1 200px;
}
.TextBlock div,h3{
    font-size: 1.3rem;
}
.TextBlock div,p{

    float: left;
}
@media(max-width: 1200px){
    .Functions{
        width: 100%;
        height: 950px;
    }
    .TextBlock{
        margin-left: 40px;
    }
}