.Cálculos{
    width: 100%;
    height: 530px;
}
.Título{
    padding: 5px;
    text-align: center;
    font-size: 1.5rem;
    font-family: sans-serif;
}
.Seleção{
    display: flex;
    width: 100%;
}
.Seleção a{
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: black;
    padding: 5px;
}
.Seleção div{
    text-align: center;
    width: 80px;
    margin-left: 15px;
    border: 1px solid rgba(0, 0, 0, 0.404);
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.397);
}
.Seleção a:hover{
    color: rgba(0, 0, 0, 0.589);
    transition: 0.3s;
    border-bottom: 1px solid black;
}
.Formulator{
    margin: 0 auto;
}
